:root {
    font-size: 8px;
}
* {
    position: relative;
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: sans-serif;
    font-size: 2rem;
}
a {
    text-decoration: none;
    color: inherit;
}
h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}
button {
    cursor: pointer;
}
ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
}
#root {
    min-height: 100vh;
    display: grid;
    grid-template-rows: auto 1fr auto;
}